<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">用户管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/property/detail', query: { pageState: 1 }}"> 导出用户数据</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="infomationList"
                style="width: 100%">
        <el-table-column label="图像"
                        min-width="150">
          <template slot-scope="scope">
            <img :src="scope.row.image" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="title"
                        label="微信名"
                        min-width="150">
        </el-table-column>
        <el-table-column prop="desc"
                        label="open Id"
                        min-width="150"
                        show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="public_at"
                        label="手机号"
                        min-width="150"
                        show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="public_at"
                        label="地址"
                        min-width="150"
                        show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="我的总关注数"
                        min-width="150"
                        show-overflow-tooltip>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      infomationList: [],
      curPage: 1,
      pageSize: 20,
      total: 100
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryInfoList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.informationList(params, 'get').then((res) => {
        // let result = res.data
        // this.infomationList = result.informations || []
        // this.curPage = result.page.current_page
        // this.pageSize = result.page.per
        // this.total = result.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
    },

    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
    },

    // 编辑操作
    editHandleClick (row) {
      this.$router.push({
        path: '/property/detail',
        query: {
          pageState: 2,
          property_id: row.id
        }
      })
    },

    // 删除操作
    deleteHandleClick (row) {
      let params = {
        information_id: row.id
      }
      this.$http.deteleInfomation(params, 'delete').then(res => {
        let result = res.data.success
        if (result) {
          this.queryInfoList()
        }
      })
    }
  },
  created () {
    this.queryInfoList()
  }
}
</script>

